.login {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__form {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__texto {
    color: #f29166;
    margin-bottom: 30px;
  }

  &__link {
    color: #2e7f7b;
  }

  @media screen and (max-width: 768px) {
    margin-bottom: 70%;
  }
}
